.inputForm{
     background-color: rgb(108, 132, 163);
     box-shadow: -1px 6px 50px 45px rgba(50,50,50,0.75);
     margin-top: 50px;
     padding: 20px;
     border-radius: 5px;
     width: 350px;
     border-style: dashed;
     border-color:rgb(41, 70, 107) ;
}
.inputForm input,textarea{
    width: 300px;
    border: 1px solid white;
    padding: 1px 10px;
    outline: none;
    margin-bottom: 20px;
    font-size: 16px;
}
.inputForm button{
    background-color: blue;
    color: white;
    border: none;
    padding: 5px 25px;
    font-size: 14px;
    font-weight: bold;
    border-radius: 10px;
    margin-left: 100px;
}